import * as type from './actions/actionTypes'

const initialState = {
  isDataLoaded: true,
  userInfo: {},
  disasterArea: '',
  disasterSummary: {}
}

const reducer = (state = initialState, action) => {
  switch (action.type) {
    case type.GET_USER_INFO:
      return {
        ...state,
        isDataLoaded: true,
        userInfo: {...action.userInfo}
      }
    case type.GET_DISASTER_SUMMARY:
      const disasterSummary = action.payload.disasterSummary
      const newDisasterSummary = Object.keys(disasterSummary).reduce((tempSummary, key) => {
        const disasterArea = disasterSummary[key].CaseLocationDistrict
        if (disasterArea in tempSummary) {
          tempSummary[disasterArea].push(disasterSummary[key])
        } else {
          tempSummary[disasterArea] = [disasterSummary[key]]
        }
        return tempSummary
      }, {})
      return {
        ...state,
        isDataLoaded: true,
        disasterSummary: newDisasterSummary
      }
    case type.SET_DATA_LOADED:
      return {
        ...state,
        isDataLoaded: action.payload.isDataLoaded
      }
    case type.TOGGLE_MOBILE_DRAWER:
      return {
        ...state,
        isMobileOpen: !state.isMobileOpen
      }
    case type.DISASTER_AREA_CHANGE:
      return {
        ...state,
        disasterArea: action.payload.disasterArea
      }
    default:
      return state
  }
}

export default reducer



