import React from 'react'
import Loadable from 'react-loadable'

import AppLoading from './container/AppLoading'

import WarningIcon from '@material-ui/icons/Warning'

const lazyloadComponent = (componentName) => {
  return Loadable({
    loader: () => import(`./container/${componentName}`),
    loading: AppLoading
  })
}

const RouteConfig = [
  {
    page: '災害統計',
    path: '/disasterSummary',
    component: lazyloadComponent('DisasterSummary'),
    icon: <WarningIcon />
  }
]

export default RouteConfig



